import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import BootstrapTable from "react-bootstrap-table-next";
import { setTotalBon } from "../../actions/TrasferCustomerAction";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormValidasiTransferCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "namaToko",
          text: "Nama Toko",
        },
        {
          dataField: "noBon",
          text: "No Bon",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "noRekening",
          text: "Nomor Rekening",
        },
        {
          dataField: "saldoRp",
          text: "Saldo (Rp)",
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-success mr-2"
                  data-toggle="modal"
                  data-target="#primary-header-modal"
                  onClick={() => this.props.dispatch(setTotalBon(row.saldoRp))}
                >
                  <i className="fa fa-check-circle mr-1"></i>
                  VALIDASI
                </button>
              </div>
            );
          },
        },
      ],
      products: [
        {
          tanggal: "001",
          namaToko: "BANDUNG",
          noBon: "001",
          keterangan: "TEst",
          noRekening: "8888",
          saldoRp: "1000000",
        },
      ],
      searchText: "",
    };
  }

  onChange() {
    this.props.change("cashNetto", this.props.cashNetto);
    this.props.change("transferNetto", this.props.transferNetto);
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  searchList() {
    console.log(this.state.searchText);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <div className="form-group">
              <label htmlFor="">Kriteria</label>
              <select
                className="form-control"
                id="kriteria"
                name="kriteria"
                defaultValue="tanggalJanji"
              >
                <option value="DEFAULT" key="DEFAULT">
                  Tanggal Janji
                </option>
              </select>
            </div>
          </div>
          <div className="col-lg-3">
            <div className="form-group">
              <label htmlFor="">Tanggal Janji</label>
              <input
                type="date"
                id="searchNoBon"
                onChange={this.inputChange("searchText")}
                className="form-control"
              />
            </div>
          </div>
          <div className="col-lg-1">
            <div className="form-group">
              <label htmlFor="" className="text-white">
                .
              </label>
              <button
                type="button"
                className="btn btn-primary form-control"
                onClick={() => this.searchList()}
              >
                Cari
              </button>
            </div>
          </div>
          <div className="col-md-12 col-lg-12 mt-3">
            <BootstrapTable
              keyField="noTransaksi"
              data={this.state.products}
              columns={this.state.columns}
              bordered={true}
              headerClasses="header-table"
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormValidasiTransferCustomer = reduxForm({
  form: "FormValidasiTransferCustomer",
  enableReinitialize: true,
})(FormValidasiTransferCustomer);
const selector = formValueSelector("FormValidasiTransferCustomer"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer, ciok, rongsok } = selector(
    state,
    "harga",
    "cash",
    "transfer",
    "ciok",
    "rongsok"
  );
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
  };
})(FormValidasiTransferCustomer);
