import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const maptoState = (state) => {
  return {
    initialValues: {
      totalBon: state.provinsi.totalBon,
    },
  };
};

class Validasi extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-4 col-md-4">
              <Field
                name="cash"
                label="Cash"
                component={renderField}
                type="number"
              />
            </div>
            {this.props.from === "transfer" ? (
              <>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="transfer"
                    label="Transfer"
                    component={renderField}
                    type="number"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="tanggal"
                    label="Tanggal"
                    component={renderField}
                    type="date"
                  ></Field>
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="a/cAsal"
                    label="A/C Asal"
                    component={renderField}
                    type="text"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <label htmlFor="">A/C Tujuan</label>
                  <Field
                    name="a/cTujuan"
                    component="select"
                    className="form-control"
                  >
                    <option value="DEFAULT" key="DEFAULT">
                      Silahkan Pilih
                    </option>
                  </Field>
                </div>
                <div className="col-lg-4 col-md-4"></div>
              </>
            ) : (
              <>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="giro"
                    label="Giro"
                    component={renderField}
                    type="number"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="noGiro"
                    label="No Giro"
                    component={renderField}
                    type="number"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="bank"
                    label="Bank"
                    component={renderField}
                    type="number"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="tanggalJ/T"
                    label="Tanggal Jatuh Tempo"
                    component={renderField}
                    type="number"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="tanggal"
                    label="Tanggal"
                    component={renderField}
                    type="date"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <div className="form-group">
                    <label htmlFor="">A/C Tujuan</label>
                    <Field
                      name="a/cTujuan"
                      component="select"
                      className="form-control"
                    >
                      <option value="DEFAULT" key="DEFAULT">
                        Silahkan Pilih
                      </option>
                    </Field>
                  </div>
                </div>
                <div className="col-lg-8 col-md-8">
                  <div></div>
                </div>
              </>
            )}
            <div className="col-lg-4 col-md-4">
              <Field
                name={this.props.from === "transfer" ? "totalBon" : "awal"}
                label={this.props.from === "transfer" ? "Total Bon" : "Awal"}
                component={renderField}
                type="number"
                readOnly={true}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="totalBayar"
                label="Total Bayar"
                component={renderField}
                type="number"
                readOnly={true}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name={this.props.from === "transfer" ? "sisaBon" : "akhir"}
                label={this.props.from === "transfer" ? "Sisa Bon" : "Akhir"}
                component={renderField}
                type="text"
                readOnly={true}
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  Tambah
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

Validasi = reduxForm({
  form: "Validasi",
  enableReinitialize: true,
})(Validasi);
export default connect(maptoState, null)(Validasi);
